"use client";

import { useState } from "react";
import { Button } from "@workspace/ui/components/button";
import { Sheet, SheetTrigger } from "@workspace/ui/components/sheet";
import { Users } from "lucide-react";
import ProfileForm from "./ProfileForm";

export default function ProfileHeader({ profile }: { profile: any }) {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <div className="flex flex-col sm:flex-row justify-between items-center gap-4">
            <div>
                <h1 className="text-3xl font-bold">{profile.name}</h1>
                <p className="text-sm text-gray-500">{profile.position}</p>
            </div>

            <Sheet open={isOpen} onOpenChange={setIsOpen}>
                <SheetTrigger asChild>
                    <Button className="bg-emerald-600 text-white flex items-center">
                        <Users className="mr-2 w-4 h-4" /> Edit Profile
                    </Button>
                </SheetTrigger>
                <ProfileForm profile={profile} onClose={() => setIsOpen(false)} />
            </Sheet>
        </div>
    );
}
