"use client";

import { useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { Button } from "@workspace/ui/components/button";
import { Form, FormField, FormItem, FormLabel, FormControl, FormMessage } from "@workspace/ui/components/form";
import { Input } from "@workspace/ui/components/input";

const schema = z.object({
    name: z.string().min(1, "Name is required"),
    dob: z.string().min(1),
    position: z.string().min(1),
});

export default function ProfileForm({ profile, onClose }: any) {
    const form = useForm({
        resolver: zodResolver(schema),
        defaultValues: {
            name: profile?.name || "",
            dob: profile?.dob || "",
            position: profile?.position || "",
        },
    });

    const onSubmit = async (values: any) => {
        console.log("Updated profile", values);
        onClose();
    };

    return (
        <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 p-4">
                <FormField
                    control={form.control}
                    name="name"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Name</FormLabel>
                            <FormControl>
                                <Input {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <Button type="submit" className="bg-emerald-600 text-white w-full">
                    Save
                </Button>
            </form>
        </Form>
    );
}
